import React from 'react';
import {FooterContainer, FooterSubscription, FooterSubHeading, FooterSubText, Form, FormInput, FooterLinksContainer, FooterLinksWrapper, FooterLinksItems, FooterLinkTitle, FooterLink, SocialMedia, SocialMediaWrap, SocialLogo, WebsiteRights, SocialIcons, SocialIconLink, SocialIcon} from './Footer.elements';
import {Button} from '../../globalStyles'
import { FaLinkedin, FaTelegram, FaTwitter, FaDiscord } from 'react-icons/fa';

const Footer = () => {
    return (
        <FooterContainer>
            <FooterSubscription>
                <FooterSubHeading>
                    Join the underworld now to build your empire!
                </FooterSubHeading>
                <FooterSubText>You can cash-out at any time.</FooterSubText>
                    <Form>
                        <FormInput name="email" type="email" placeholder="Your Email" />
                            <Button fontBig>Subscribe</Button>

                    </Form>
            </FooterSubscription>

            <FooterLinksContainer>
                <FooterLinksWrapper>
                    <FooterLinksItems>
                    <FooterLinkTitle>
                        About Us
                    </FooterLinkTitle>
                        <FooterLink to='/'>The Team</FooterLink>
                        <FooterLink to='/'>Tokenomics</FooterLink>
                        <FooterLink to='/'>Roadmap</FooterLink>


                    </FooterLinksItems>  
                    <FooterLinksItems>
                    <FooterLinkTitle>
                        Social Media
                    </FooterLinkTitle>
                        <FooterLink to='/'>Twitter</FooterLink>
                        <FooterLink to='/'>Telegram</FooterLink>
                        <FooterLink to='/'>Discord</FooterLink>
                        <FooterLink to='/'>Reddit</FooterLink>
                    </FooterLinksItems>  
                </FooterLinksWrapper>
                <FooterLinksWrapper>
                    
                    <FooterLinksItems>
                    <FooterLinkTitle>
                        Trade GANG
                    </FooterLinkTitle>
                        <FooterLink to='/'>Trade $GANG on PancakeSwap</FooterLink>
                        <FooterLink to='/'>$GANG/BNB Liquidity Pool</FooterLink>
                    </FooterLinksItems>  
                    <FooterLinksItems>
                    <FooterLinkTitle>
                        Contract Info
                    </FooterLinkTitle>
                        <FooterLink to='/'>Contract</FooterLink>
                        <FooterLink to='/'>Functions explained</FooterLink>

                    </FooterLinksItems>  
                </FooterLinksWrapper>
            </FooterLinksContainer>
            <SocialMedia>
                <SocialMediaWrap>
                    <SocialLogo to="/">
                        <SocialIcon src={require("../../images/logo_gang_101522.jpg").default} to="/" />
                        Blockgangsters
                        </SocialLogo>
                        <WebsiteRights>BlockGangsters © 2021, All rights reserved</WebsiteRights>
                        <SocialIcons>
                            <SocialIconLink href="/" target="_blank" aria-label="LinkedIn">
                                <FaLinkedin />
                            </SocialIconLink>
                            <SocialIconLink href="/" target="_blank" aria-label="Discord">
                                <FaDiscord />
                            </SocialIconLink>
                            <SocialIconLink href="/" target="_blank" aria-label="Twitter">
                                <FaTwitter />
                            </SocialIconLink>
                            <SocialIconLink href="/" target="_blank" aria-label="Telegram">
                                <FaTelegram />
                            </SocialIconLink>

                        
                    
                    </SocialIcons>
                </SocialMediaWrap>
            </SocialMedia>
        </FooterContainer>
    )
}

export default Footer